<template>
  <div class="multidocumentprint">
    <div>
      <v-card-title></v-card-title>

      <v-card-text class="mt-3">
        <!-- 畫面顯示內容區域 -->
        <h3>{{cData.company_name}}</h3>

        <div class="text-center border-bottom my-2">
          <h1>
            <strong>住民身心社會健康評估表</strong>
          </h1>
        </div>

        <table border="2px solid #00" style="width:100%">
          <tbody>
            <tr>
              <td colspan="9" class="p-2">
                <v-row class="form_title">
                  <v-col cols="2">病歷號: {{uData.p_code}}</v-col>
                  <v-col cols="2">床號: {{uData.bed_name}}</v-col>
                  <v-col cols="2">姓名: {{uData.name}}</v-col>
                  <v-col cols="2">性別: {{uData.sex}}</v-col>
                  <v-col cols="2">生日: {{uData.birthday}}</v-col>
                  <v-col cols="2">年齡: {{age}}</v-col>
                  <v-col cols="3">入住日期: {{uData.in_date}}</v-col>
                  <v-col cols="9">疾病史: {{uData.hadadisease}}</v-col>
                </v-row>
              </td>
            </tr>
          </tbody>
          
          <tbody>
            <tr class="text-center font-weight-bold">
              <td colspan="4">評估日期</td>
              <td v-for="index in 5" :key="index" width="8%">
                {{matchDate[index-1]}}
              </td>
            </tr>
          </tbody>

          <recordprint v-if="choiceDocuments.record"></recordprint>
          <kbprint v-if="choiceDocuments.kb"></kbprint>
          <fallprint v-if="choiceDocuments.fall"></fallprint>
          <bradenprint v-if="choiceDocuments.braden"></bradenprint>
          <spmsqprint v-if="choiceDocuments.spmsq"></spmsqprint>

          <tr>
            <td colspan="4" class="text-center font-weight-bold">簽名</td>
            <td v-for="index in 5" :key="index"></td>
          </tr>
        </table>

        
      </v-card-text>

      <v-divider></v-divider>


      
      <!-- <pre>{{ printData }}</pre> -->
    </div>
  </div>
</template>

<script>
import qs from "qs";
import dayjs from "dayjs";
import recordprint from "@/views/print/RecordPrint.vue";
import kbprint from "@/views/print/KbPrint.vue";
import fallprint from "@/views/print/FallPrint.vue";
import bradenprint from "@/views/print/BradenPrint.vue";
import spmsqprint from "@/views/print/SpmsqPrint.vue";

export default {
  components:{recordprint,kbprint,fallprint,bradenprint,spmsqprint},
  data() {
    return {
      cData: {},
      pData: {},
      uData: {},
      list: {}, //表單內資料
      printData:[],
      matchDate:[],
      choiceDocuments:{},
      // nursingassessmentItems: {},
      // nutritionItems: [],
      // recordItems: []
    };
  },

  computed: {
    age() {
      return dayjs().format("YYYY") - dayjs(this.uData.birthday).format("YYYY");
    },
  },

  mounted() {
    this.cData = JSON.parse(sessionStorage.getItem("cData"));
    this.pData = JSON.parse(sessionStorage.getItem("pData"));
    this.uData = JSON.parse(sessionStorage.getItem("uData"));
    this.printData = JSON.parse(sessionStorage.getItem("printData"));
    this.matchDate = this.printData.matchDate;
    this.choiceDocuments = this.printData.choiceDocuments;
    
  },

  methods: {

  }
};
</script>

<style lang="scss">
.multidocumentprint {
  @import "node_modules/bootstrap/scss/bootstrap";
  @import "node_modules/bootstrap-vue/src/index.scss";

  .form_title {
    font-size: 1.2rem;
    // text-align: center;
    font-family: "微軟正黑體";
  }
  
  td{
    font-size: 1rem;
    // border:1px #000000 solid;
  }

  .vertical-mode {
  writing-mode: tb-rl;
  -webkit-writing-mode: vertical-rl;
  writing-mode: vertical-rl;
  white-space:nowrap;
}
}
</style>